body {
  background-color: #f3f3f3;
}
.home {
  height: 100vh;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainForm {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.searchInput {
  display: inline-block;
  width: 60%;
  border: 2px solid #bebcc7;
  outline: none;
  margin: 1rem;
  margin-top: 2rem;
  height: 1.2rem;
  border-radius: 0.5rem;
  letter-spacing: 2px;
  padding: 0.5rem 1rem;
}
input[type="text"]:focus {
  background-color: #fff;
  border: 2px solid #8c80b9;
  padding: 0.5rem 1rem;
  margin: 1rem;
  margin-top: 2rem;
}

.submit {
  height: 2.5rem;
  width: 4rem;
  border-radius: 0.5rem;
  background-color: #8c80b9;
  color: #fff;
  border: none;
  margin-top: 1rem;
}
.submit:focus {
  outline: none;
}

.submit:active {
  border-style: none;
  color: #fff;
  background-color: #000;
  margin-top: 1rem;
}
.pagination {
  text-align: center;
  color: #7739c9;
}
option {
  color: #7739c9;
  background-color: #e8e3e9;
}
.portfolio-div {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10px;
  padding: 10px;
}

.portfolio-image {
  width: 100%;
  border-radius: 0.5rem;
  cursor: pointer;
}
.portfolio-image:hover {
  border-radius: 2rem;
  box-shadow: 5px 5px 10px #8c80b9, -5px -5px 10px #9a84a1;
}

.nav {
  display: inline-block;
  border-bottom: 2px solid #8c80b9;
  width: 100%;
  height: 2rem;
  box-shadow: 1px 1px 1px #8c80b9;
}
.logo {
  text-decoration: none;
  font-size: 1.4rem;
  font-weight: bolder;
  display: inline;
  margin-left: 5rem;
}
.nav-ul {
  display: inline;
}
.nav-li {
  display: inline;
  margin-right: 1rem;
}
.photo-container {
  display: grid;
  grid-template-columns: auto 15rem;
  grid-gap: 10px;
  padding: 10px;
  margin-top: 1rem;
}
.image-main-div {
  text-align: center;
}
.image-main {
  height: 70vh;
  width: 60vw;
  border-radius: 1rem;
  border: 1px solid #aa9696;
  box-shadow: 10px 10px 20px #b4b0c2, -10px -10px 20px #b4b0c2;
}
a {
  text-decoration: none;
}

p {
  font-size: 1rem;
  font-weight: 800;
}
.download {
  background-color: #07060c;
  color: #fff;
  border: none;
  margin: 1rem;
  margin-top: 1rem;
  padding: 0.5rem;
  border-radius: 5px;
}
.download:focus {
  outline: none;
}

.download:active {
  border-style: none;
  color: #fff;
  background-color: #7739c9;
  margin-top: 1rem;
}
.home-title {
  margin: 2rem;
  text-align: center;
  color: #be7726;
}
.home-pic {
  margin: 2rem;
  width: 90%;
  height: 60vh;
}
.home-icon {
  margin-top: 1rem;
  font-size: 2rem;
}
.final-div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10rem;
  border: 5px solid #b4b0c2;
  margin-top: 1rem;
}
.paragraph {
  font-size: 1.3rem;
  color: #470e5e;
}
.footer {
  text-align: center;
}

@media screen and (max-width: 360px) {
  .logo {
    text-decoration: none;
    font-size: 1rem;
    font-weight: bolder;
    display: inline;
    margin-left: 1rem;
  }
  .portfolio-div {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 5px;
    padding: 5px;
  }
  .photo-container {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 10px;
    padding: 10px;
  }
  .image-main {
    height: 70vh;
    width: 90vw;
    border-radius: 1rem;
    border: 1px solid #aa9696;
    box-shadow: 10px 10px 20px #b4b0c2, -10px -10px 20px #b4b0c2;
  }
  .details {
    text-align: center;
    margin: 1rem;
  }
  .final-div {
    text-align: center;
    height: 20rem;
    border: 5px solid #b4b0c2;
    margin: 1rem;
  }
  .paragraph {
    font-size: 0.7rem;
    color: #470e5e;
  }
}
